.container {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax($block-size, 1fr));

  &__block {
    display: flex;
    justify-content: center;
    align-items: center;
    height: $block-size;
    color: white;

    @each $name, $color in $block-colors {
      &--#{$name} {
        background-color: $color;
      }
    }
  }

  @media (min-width: 1200px) {
    grid-template-columns: repeat(3, 1fr);
  }
}
